<template>
  <div id="container" v-loading.body="loading"  element-loading-text="拼命加载中">
    <el-row type="flex" justify="center" v-if="dataStatus">

      <el-col :span="23">
        <el-tabs  @tab-click="handleClick" v-model="activeName">
          <el-tab-pane label="办会人信息" name="user" ></el-tab-pane>

        </el-tabs>
        <router-view @setCustomerStatusId="setCustomerStatusId"  @querySuc="removeLoading"></router-view>
      </el-col>
    </el-row>

  </div>

</template>
<script>
export default {
  name: 'contentWrap',
  data: function () {
    return {
      activeName: 'user',
      loading: true, // 是否显示详情和编辑loading状态
      CustomerStatusId:''
    }
  },
  props:['dataStatus'],
  mounted() {
    let self = this;
    this.$root.Bus.$on('listIsNull', () => {
      self.loading = false;
    })
    self.routerChange();
  },
  methods: {
    handleClick(tab, event) {
      // var self = this;
      // var urlName = tab.name
      // self.$router.push({'path':'/index/'+urlName})
    },
    removeLoading() {
      this.loading = false;
    },
    routerChange(){//监控路由离开创建页面，请求清除创建限制时间。
      let self = this;
      self.$router.beforeEach((to,from,next)=>{
        let id = self.CustomerStatusId;
        if (id!=""&&from.name=='user'&&from.path!=to.path){
          let mobile = from.params.mobile;
          let tMobile =to.params.mobile;
          let companyId = from.params.company_id;
          let tCompanyId = to.params.company_id;
          let localStorageItemName = `draft_${mobile}${companyId}`;
          //判断是在创建页面有未保存数据的情况下离开页面，且并不是创建后离开的
          if ((companyId!=tCompanyId||mobile!=tMobile)&&!!localStorage.getItem(localStorageItemName)){
            self.$confirm('有数据没有保存, 是否继续跳转?', '提示', {
              confirmButtonText: '确定',
              cancelButtonText: '取消',
              type: 'warning'
            }).then(() => {
              self.$root.Bus.$emit('tagDraft');
              self.$ajax.post(self.$api.getModuleHost('CRM', "/Customer/delCustomerStatus"),{id:id})
                .then(data=>{
                  if (data.code ==1000){
                    next()
                  }
                })
                .catch(error=>{
                  console.log(error)
                })
            }).catch(() => {
              next(false)
              return;
            });
          }else {
            self.$ajax.post(self.$api.getModuleHost('CRM', "/Customer/delCustomerStatus"),{id:id})
              .then(data=>{
                if (data.code ==1000){
                  next()
                }
              })
              .catch(error=>{
                console.log(error)
              })
          }
        }else {
          next();
          self.loading = true;
        }
      })
    },
    setCustomerStatusId(id){
      this.CustomerStatusId=id
    }
  }
}
</script>
<style lang="less">
  #container {
    min-height: 100vh;
    flex: 1 auto;
    position:relative;
  }
  .el-message{
    top:50%;
    margin-top:-20;
  }
  .el-form-item__label,
  .el-radio__label,
  .el-checkbox__label,
  .el-input,
  .el-textarea__inner,
  .el-form-item__content,
  .el-button
  {
    font-size:12px;
  }
  .el-loading-spinner{
    top:200px;
  }
  .el-form-item__error{
    padding-top: 0;
  }

</style>



