<template>

  <div class="container">
      <user-list v-show="from" ref="userList"></user-list>
      <content-wrap :dataStatus="listData" ></content-wrap>
  </div>
</template>

<script>

import UserList from '../components/index/UserList.vue'
import ContentWrap from '../components/index/ContentWrap.vue'
export default {
  name: 'IndexPage',
  data: function () {
    return {
      from: true, // 如果是boss链接过来查看的操作 不显示列表，只显示详情
      listData: false, // 控制列表中是否有数据的显示开关
    }
  },
  mounted() {
    let fromName =  this.$route.query.from ;
    if (fromName == 'boss') {
      this.from = false;
    }

  },
  methods: {

  },

  components:{
    UserList,
    ContentWrap
  }
}
</script>
<style lang="less">
.container {
  display: flex;
  min-width: 1280px;
}
  .pointer{
    cursor:pointer;
  }
  .el-form-item{
    margin-bottom: 14px;
  }
</style>



