<template>
	<div id="nav-menu">
		<el-tabs v-model="activeName" type="border-card" @tab-click="changeTab">
			<el-tab-pane label="全部" name="all">
				<!-- fiter condition -->
				<el-row class="filter-wrap">
					<el-col :span="7">
							<div class="grid-content">
								<h3>共{{pagination.total}}个客户</h3>
							</div>
					</el-col>
					<el-col :span="7">
						<div class="grid-content" >
							<el-select v-model="filter" @change="setFilterParam" key="all-filter">
								<el-option
									v-for="item in filters"
									:key="item.value"
									:label="item.label"
									:value="item.value">
								</el-option>
							</el-select>
						</div>
					</el-col>
					<el-col :span="10">
						<div class="grid-content">
							<el-select v-model="rank" class="rank-list" @change="setRankParam" key="all-rank">
								<el-option
									v-for="item in ranks"
									:key="item.value"
									:label="item.label"
									:value="item.value">
								</el-option>
							</el-select>
						</div>
					</el-col>
				</el-row>
				<!-- client list -->
				<el-menu default-active="" class="client-list" >
            <el-menu-item  v-for="(val, key) in clientList"
              :class="{hackActive:$route.path==('/foreground/index/'+(val.is_created == 0? 'user': 'userview')+'/'+val.mobile+'/'+val.company_id+'/'+val.customer_id)}"
              :index="''+key">
              <router-link :to="'/foreground/index/'+(val.is_created == 0? 'user': 'userview')+'/'+val.mobile+'/'+val.company_id+'/'+val.customer_id">
                <el-col :span="18">
                  <div class="clearfix">
                    <el-col :span="5">
                      <el-tag type="danger">{{val.decision_user}}</el-tag>
                    </el-col>
                    <el-col :span="5">
                      {{val.customer_name}}
                    </el-col>
                    <el-col :span="13" class="company-name">
                      <span>
                        {{val.company_name}}&nbsp;
                      </span>

                    </el-col>
                    <el-col :span="1" class="draft" v-if="val.is_draft">
                        [草稿]
                    </el-col>
                  </div>
                  <div  class="clearfix info-btm">

                    <el-col :span="12">
                      总成交金额：{{val.deal_amount}}元
                    </el-col>
                    <el-col :span="12">
                      最近成交时间：{{val.deal_time}}
                    </el-col>
                  </div>
                </el-col>
                <el-col :span="6">
                    <el-button size="small" type="primary" class="client-check-btn">{{val.is_created == 0? '创建客户': '查看信息'}}</el-button>
                </el-col>
              </router-link>
            </el-menu-item>

				</el-menu>

        <!-- pagination -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.current_page"
          :page-size="20"
          layout="total, prev, pager, next"
          :total="pagination.total"
          class="pagination"
          >
        </el-pagination>
      </el-tab-pane>
      <el-tab-pane label="新成交客户" name="new">
        <!-- fiter condition -->
        <el-row class="filter-wrap">
          <el-col :span="7">
              <div class="grid-content">
                <h3>共{{pagination.total}}个客户</h3>
              </div>
          </el-col>
          <el-col :span="7">
            <div class="grid-content">
              <el-select v-model="filter"  @change="setFilterParam" key="new-filter">
                <el-option
                  v-for="item in filters"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </el-col>
          <el-col :span="10">
            <div class="grid-content">
              <el-select v-model="rank" class="rank-list" @change="setRankParam" key="new-rank">
                <el-option
                  v-for="item in ranks"
                  :key="item.value"
                  :label="item.label"
                  :value="item.value">
                </el-option>
              </el-select>
            </div>
          </el-col>
        </el-row>
        <!-- client list -->
        <el-menu default-active="0" class="client-list" >
          <el-menu-item :class="{hackActive:$route.path==('/foreground/index/'+(val.is_created == 0? 'user': 'userview')+'/'+val.mobile+'/'+val.company_id+'/'+val.customer_id)}"  v-for="(val, key) in clientList" :index="''+key">
						<router-link :to="'/foreground/index/'+(val.is_created == 0? 'user': 'userview')+'/'+val.mobile+'/'+val.company_id+'/'+val.customer_id">
							<el-col :span="18">
								<div class="clearfix">
									<el-col :span="5">
										<el-tag type="danger">{{val.decision_user}}</el-tag>
									</el-col>
									<el-col :span="5">
										{{val.customer_name}}
									</el-col>
									<el-col :span="13" class="company-name">
										<span>
											{{val.company_name}}&nbsp;
										</span>

									</el-col>
									<el-col :span="1" class="draft" v-if="val.is_draft">
											[草稿]
									</el-col>
								</div>
								<div  class="clearfix info-btm">

									<el-col :span="12">
										总成交金额：{{val.deal_amount}}元
									</el-col>
									<el-col :span="12">
										最近成交时间：{{val.deal_time}}
									</el-col>
								</div>
							</el-col>
							<el-col :span="6">
									<el-button size="small" type="primary" class="client-check-btn">{{val.is_created == 0? '创建客户': '查看信息'}}</el-button>
							</el-col>
						</router-link>
          </el-menu-item>
        </el-menu>

				<!-- pagination -->
				<el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="pagination.current_page"
          :page-size="20"
          layout="total, prev, pager, next"
          :total="pagination.total"
          class="pagination"
          >
        </el-pagination>

			</el-tab-pane>

		</el-tabs>

	</div>
</template>
<script>
export default {
	name: 'UserList',
	data: function () {
		return {
			activeName: 'all',
			filter: "全部客户",
			rank: "默认排序",
			selectChange:false,
			clientParams:{
				"current_page": "1",
				"filter": "0",
				"rank": "0",
				"is_new": "0"
			},
			filters: [{
				 value: '0',
				 label: '全部客户'
			 }, {
				 value: '1',
				 label: '决策人'
			 }, {
				 value: '2',
				 label: '非决策人'
			 },{
        value: '3',
        label: '未完善'
      }],
			 ranks: [{
					value: '0',
					label: '默认排序'
				},{
					value: '1',
					label: '按成交金额排序'
				}, {
					value: '2',
					label: '按最近成交排序'
				}],
				clientList:{},
				pagination:{},
		}
	},
	mounted () {
		let self = this;
		// 判断页面来源，根据来源请求参数不同
		if(self.$route.query.from) {
			let fromName =  self.$route.query.from ;
				if (fromName == 'boss' || fromName == 'bossAdd') {
				let mobile = self.$route.query.mobile
				let companyId = self.$route.query.companyId

				if (self.$route.query.type) {
					self.clientParams['type'] = self.$route.query.type;
				}
				if (self.$route.query.checkType) {
					self.clientParams['checkType'] = self.$route.query.checkType;
				}
				self.clientParams['from'] = fromName;
				self.clientParams['mobile'] = mobile;
				self.clientParams['company_id'] = companyId;
			}
		}
		this.getClientList()
		this.$root.Bus.$on('addSuc', (value) => {
			let vMobile = value[0];
			let vCompanyId = value[1];
			for (let v in self.clientList) {
				  let mobile = self.clientList[v]['mobile'];
				  let company_id = self.clientList[v]['company_id'];
					if (mobile == vMobile && company_id == vCompanyId){
						self.clientList[v]['is_created'] = 1;
					}
			}

		})
		self.$root.Bus.$on('tagDraft',() => { // 切换客户后，给客户打草稿标签
			let ls = window.localStorage
			for (let v in self.clientList) {
					let company_id = self.clientList[v]['company_id'];
					let mobile = self.clientList[v]['mobile'];
					let draftId = 'draft_'+mobile+company_id
					self.clientList[v]['is_draft'] = false;
					if (ls[draftId]) {
						self.clientList[v]['is_draft'] = true;
					}
			}
		});
		this.$bus.$off('flashUserList');
		this.$bus.$on('flashUserList',self.getClientList)
	},
	methods: {

		setFilterParam(v) {
			if (this.selectChange == false) {
				this.clientParams.filter = v;
				this.getClientList(1);
				this.selectChange = true;
			}
		},
		setRankParam(v) {
			if (this.selectChange == false) {
				this.clientParams.rank = v;
				this.getClientList(1);
				this.selectChange = true;
			}
		},
		getClientList(arg) {
			let self = this;
			if (!!arg&&arg == 1){
        self.clientParams.current_page=1
      }
			self.$ajax.get(self.$api.getModuleHost('CRM', "/customer/getClientList"),self.clientParams).then(function (data) {
					self.selectChange = false;
					if (data.code == 1000) {
							self.$parent.listData = true; // 在父元素中控制列表数据的状态，如果有值就在右侧内容区域显示编辑或查看，没有则显示没有数据
              self.clientList = data.data;
              // 根据 localStorage 判断是否有草稿的数据
              if(window.localStorage){
                let ls = window.localStorage
                for (let v in self.clientList) {
                    let company_id = self.clientList[v]['company_id'];
                    let mobile = self.clientList[v]['mobile'];
                    let draftId = 'draft_'+mobile+company_id
                    self.clientList[v]['is_draft'] = false;
                    if (ls[draftId] && self.clientList[v]['is_created'] == 0) {
                      self.clientList[v]['is_draft'] = true;
                    } else {
											ls.removeItem(draftId);
										}
                }
              }
              self.pagination.current_page = data.current_page;
              self.pagination.total = data.total;
            let routeMobile=!!self.$route.params.mobile?self.$route.params.mobile:self.clientList[0]['mobile']
            let routeCompanyId=!!self.$route.params.company_id?self.$route.params.company_id:self.clientList[0]['company_id']
            let routeCustomerId=!!self.$route.params.customer_id!=""?self.$route.params.customer_id:self.clientList[0]['customer_id']
            self.$router.push({ path:`/foreground/index/${routeCustomerId==0?'user':'userview'}/${routeMobile}/${routeCompanyId}/${routeCustomerId}`})
          } else {
							self.clientList = {};
							self.$root.Bus.$emit('listIsNull');
							self.$parent.listData = false; // 在父元素中控制列表数据的状态，如果有值就在右侧内容区域显示编辑或查看，没有则显示没有数据
							self.pagination.current_page = 1;
              self.pagination.total = 0;
              console.log("currently has none clients' data");
          }

			}).catch(function (err) {
					console.error(err);
			});
		},
		changeTab(tab, event) {
			let name = tab.name;
			this.initClientParams();
			if (name == 'all') {
				this.activeName = 'all';
				this.clientParams.is_new = 0;

				this.getClientList();
			} else {
				this.activeName = 'new';
				this.clientParams.is_new = 1;
				this.getClientList();
			}

		},
		initClientParams() {
			this.filter = "全部客户";
			this.rank = "默认排序";
			this.clientParams.current_page = "1";
			this.clientParams.filter = "0";
			this.clientParams.rank = "0";
		},
		handleSizeChange(val) {
			// console.log(`每页 ${val} 条`);
		 },
		 handleCurrentChange(val) {
			 this.clientParams.current_page = val;
			 this.getClientList();
		 }
	},
}
</script>
<style lang="less">
	#nav-menu {
		width: 500px;
		min-width: 500px;
		min-height:100vh;
		border-radius: 0;
		overflow-y:auto;
		background:#fff;
		border: 1px solid #d1dbe5;
		box-sizing:border-box;
		.el-menu {
			background-color: #fff;
		}
		.el-menu-item,
		.el-submenu .el-submenu__title {
			padding-top:10px;
			height:70px;
			line-height:20px;
			&:hover {
				background:#EFF2F7
			}
		}
		.info-btm{
			margin-top:10px;
		}
		.client-check-btn{
			float:right;
			margin-top:10px;
		}
		.filter-wrap{
				padding:12px 20px;
				.grid-content{
					h3{
						line-height:36px;
					}
					.rank-list{
						margin-left:10px;
					}

				}
		}

	}
	.el-tabs--border-card>.el-tabs__content {
		padding:0;
	}
	.el-tabs--border-card{
		box-shadow:none;
		border:none;
	}
	.el-menu-item.is-active{
		background:#E5E9F2;
		color:#20A0FF;
	}
	.pagination{
		margin-top:20px;
	}
	.client-list li{
		a{
        font-size:12px;
				color:#333;
		}
		border-bottom:1px solid #d1dbe5
	}
	.company-name {
		text-overflow:ellipsis;
		white-space:nowrap;
		overflow:hidden;
	}
	.draft{
		color:#ff4949;
	}
  /*userListActiveHack*/
  .el-menu-item.is-active{
    background-color:transparent;
  }
  .el-menu-item.hackActive{
    background: #E5E9F2;
  }
</style>



